<template>
  <div class="rank">
    <div class="rank-front">
      <div class="checkpoint">
        昨日榜单
      </div>
      <a class="my-dream-truck" href="https://topic.360che.com/m/2017101901/">我的梦想卡车</a>
      <a class="to-buy" href="https://topic.360che.com/m/2017110601/index.aspx">去购车</a>
    </div>
    <div class="rank-content" data-noscroll="true">
      <div class="content-title">
        <span class="rank-info">排名</span>
        <span class="score">成绩</span>
        <span class="phone">手机号</span>
      </div>
      <div class="my-rank" v-if="mydata.isgame == 1 && mydata.rank">
        <i class="my">我的</i>
        <span>{{mydata.rank}}</span>
        <span class="score">{{timeConversion(mydata.scoretime)}}</span>
        <span class="my-tel">{{mydata.tel ? mydata.tel : '未完善手机号'}}</span>
      </div>
      <div class="myRankShow" v-if="mydata.iswinn == 1">
        已获得过车模，给其他小伙伴机会哦~
      </div>
      <div class="myRankShow" v-if="(mydata.isgame == '0' || mydata.isgame == '1') &&  !mydata.rank">
        {{mydata.isgame == 0 ? '您还没有玩儿过~' : mydata.isgame == 1 ? '您的排行榜将在明天显示哦~' : ''}}
      </div>
      <ul class="rank-no-list" ref="rank-list" @scroll="loadMore" data-noscroll="true">
        <li v-for="(item, index) in rankList" data-noscroll="true">
          <span class="rank-img rank-first" v-if="index === 0"></span>
          <span class="rank-img rank-second" v-if="index === 1"></span>
          <span class="rank-img rank-third" v-if="index === 2"></span>
          <span class="rank-ranking" v-if="index > 2">{{item.rank}}</span>
          <span class="score">{{timeConversion(item.scoretime)}}</span>
          <span>{{item.tel}}</span>
        </li>
        <li class="load-more" v-show="prohibit && rankList.length >= 20" data-noscroll="true">
          <div class="more" data-noscroll="true">
              <span class="more-ring"></span> 正在加载...
          </div>
        </li>
      </ul>
      <div class="more" v-if="!rankList.length">
          <span class="more-ring"></span> 正在加载...
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    //页数
    page: 1,
    //加载状态
    loadmore: true,
    //是否还有数据
    prohibit:true,
    //我的数据
    mydata: {},
    // 排行榜列表
    rankList: [],
    meData:false,
  }),
  created() {
    //查看有没有缓存
    let rankData = sessionStorage.getItem('rankData')
    //有缓存读取缓存，并且赋值
    if(rankData && rankData != 'undefined'){
      rankData = JSON.parse(rankData);
      this.mydata = rankData.myRankData || {}
      this.rankList = rankData.rankList
      this.page = rankData.page
      this.meData = true
      //每次都去请求自身排名
      this.ajax(`/m/2017110201/getrank.aspx?page=1&size=20`,{},data => {
        if(data.data.result == 1){
          if (data.data.mydata) {
            this.mydata = data.data.mydata
          }
        }
       },err => {
         // alert('加载失败')
       })
    }else{
      //如果没有缓存 && 重新请求排行榜数据
      this.getRankData()
    }
  },
  methods: {
    loadMore (e) { // 评论滚动加载
      // 获取内容高度
      let rankContentHeight = this.$refs['rank-list'].scrollHeight
      // 获取容器高度
      let rankListHeight = this.$refs['rank-list'].offsetHeight
      // 判断滚动到底
      if (rankContentHeight - e.target.scrollTop - rankListHeight === 0 && this.loadmore && this.prohibit) {
        this.getRankData();
      }
    },
    //滚动加载更多数据
    getRankData(){
      this.loadmore = false
      this.ajax(`/m/2017110201/getrank.aspx?page=${this.page}&size=20`,{},data => {
        if(data.data.result == 1 && data.data.data.length){
          if (data.data.mydata) {
            this.mydata = data.data.mydata
          }
          this.rankList =  this.rankList.concat(data.data.data)

          //关闭加载更多弹层
          this.loadmore = true
          //增加页数
          this.page++

          //存储
          let rankData = {}
          rankData.myRankData = data.data.mydata
          rankData.rankList = this.rankList
          rankData.page = this.page
          console.log(this.page,'this.page')
          sessionStorage.setItem('rankData',JSON.stringify(rankData))

          //如果条数少于20条 && 下一次没有数据 && 禁止加载
          if (data.data.data.length < 20) {
            this.prohibit = false
          }
        }else{
          this.prohibit = false
        }
       },err => {
         alert('加载失败')
       })
    },
    //时间转换
    timeConversion (ms) {
      //分
      let minute = Math.floor(ms/1000/60);
      if(minute < 10){
        minute = '0' + minute
      }
      //秒
      let second = Math.floor((ms - minute*1000 *60)/1000)
      if(second < 10){
        second = '0' + second
      }
      //毫秒
      let millisecond = ms - minute*1000 * 60 - second * 1000
      if(millisecond < 10){
        millisecond = '00' + millisecond
      }else if(millisecond > 10 && millisecond < 100){
        millisecond = '0' + millisecond
      }

      return minute + `' ` + second + `" ` + millisecond
    },
  }
}
</script>
<style scoped>
ul, li{
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.rank{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: 'font';
  background: url('https://s.kcimg.cn/wap/images/shaveImg/rank-bg.png') no-repeat 0 0;
  background-size: cover;
}
.myRankShow{
  height:90px;
  line-height:90px;
  color: #8123dc;
  font-size:36px;
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/my-bg.png');
}
.rank-front{
  display: flex;
  flex-direction: row;
  padding: 0 50px;
  align-items: flex-end;
}
.rank-front a{
  font-size: 36px;
  line-height: 36px;
  color: yellow;
  text-decoration: underline;
  cursor: pointer;
}
@media screen and (width:320px){
  .rank-front span{
    font-size: 28px;
    line-height: 28px;
    color: yellow;
    text-decoration: underline;
    cursor: pointer;
  }
}
.my-dream-truck{
  margin-left: 28px;
}
.to-buy{
  margin-left: 18px;
}
.rank-content{
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/ranking-bg.png');
  background-size: 100% 100%;
  margin: 18px 50px 40px;
}
.content-title{
  height:90px;
  padding:0 32px;
  clear: both;
  overflow: hidden;
}
.content-title span{
  float:left;
  height:88px;
  font-size: 36px;
  line-height: 88px;
  color: yellow;
  text-align: center;
}
.content-title .rank-info{
  width:150px;
}
.content-title .phone{
  width:210px;
}
.content-title .score{
  width:220px;
}
.my-rank{
  display: flex;
  flex-direction: row;
  align-items: center;
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/my-bg.png');
  background-size: cover;
}
.my-rank span{
  font-size: 36px;
  margin-left: -20px;
  color: #8123dc;
  flex: 1;
}
.my-rank .my{
  position: absolute;
  left: 20px;
  font-style: normal;
  width: 30px;
  height: 48px;
  font-size: 24px;
  line-height: 24px;
  padding: 6px 0;
  text-align: center;
  border-radius: 4px;
  background: #8123dc;
  color: #eed809;
}
.my-rank span.my-tel{
  text-align: left;
}
.rank-no-list{
  flex:1;
  top: 80px;
  overflow-y: scroll;
  font-family: 'Avenir', Helvetica, Arial;
  -webkit-overflow-scrolling:touch;
}
.rank-list li, .rank-no-list li{
  position: relative;
  display: flex;
  flex-direction: row;
  text-align: center;
  height: 84px;
  align-items: center;
  margin: 0 32px;
}
.rank-no-list li:after{
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  border-bottom: 1px dashed #deb636;
}
.rank-no-list li:last-child:after{
  width: 0;
}
.rank-no-list li span{
  font-size: 32px;
  color: #fff;
  flex: 1;
}
.rank-img{
  position: relative;
  flex: 1;
  margin-left: -40px;
}
.rank-img:before{
  position: absolute;
  content:"";
  left: 50%;
  margin-left: -40px;
  top: 0;
  margin-top: -35px;
  width: 82px;
  height: 70px;
  background-size: 82px 70px;
}
.rank-first:before{
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/rank-first.png');
}
.rank-second:before{
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/rank-second.png');
}
.rank-third:before{
  background-image: url('https://s.kcimg.cn/wap/images/shaveImg/rank-third.png');
}
.score{
  text-align: left;
}
.rank-ranking{
  margin-left: -40px;
}
.checkpoint{float:left;width:276px;height:75px;padding-top:70px;color:#eed809;font-size:62px;font-family:'font';line-height:50px;text-align:center;background: url('https://s.kcimg.cn/wap/images/shaveImg/rank-title-bg.png') no-repeat left bottom;transform: translate3d(0,-161px,0);animation: checkpoint .3s linear .3s forwards;transform-origin: left bottom;}
  @keyframes checkpoint {
    0% {
      -webkit-transform: translate3d(0,-161px,0);
      transform: translate3d(0,-161px,0) rotate(-30deg);

    }
    20% {
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0) rotate(-20deg);
    }
    40% {
      -webkit-transform: translate3d(0,-40px,0);
      transform: translate3d(0,-40px,0) rotate(-15deg);
    }

    60% {
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0) rotate(-10deg);
    }

    85% {
      -webkit-transform: translate3d(0,-20px,0);
      transform: translate3d(0,-20px,0) rotate(-5deg);
    }

    100% {
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0) rotate(0deg);
    }
  }
/* more */
.rank-list .load-more,.rank-no-list .load-more{position:relative;margin: 30px auto;}
.more{position: absolute;left:50%;top:50%;margin-left:-85px;margin-top:-45px;width:170px;padding-top: 66px;font-size: 0;color: #999; overflow: hidden; text-align: center;font-size:18px;color:#fff;
  background: url(data:image/png;base64,) no-repeat 50% 0;
  -webkit-background-size: 384px 60px;background-size: 384px 60px;-webkit-animation: loaing-animation linear 1.5s infinite; animation: loaing-animation linear 1.5s infinite;}
.more:before{content: '';position: absolute;left: 0;top: 0;width: 100%;height: 15px;}
.more.visible{display: block;}
.more-ring{content: '';position: absolute;left: 50%;top:18px;margin-left: -54px;width: 97px; height:36px;font-size: 0;
background: url(data:image/png;base64,) no-repeat 0 0;
-webkit-background-size: 98px 32px;background-size: 98px 32px;}
.more-ring:before,.more-ring:after{content: '';position: absolute;left: 38px;top: 28px;width: 10px; height: 10px;
background: url(data:image/png;base64,) no-repeat 0 0;
-webkit-background-size: 10px 10px;background-size: 10px 10px; -webkit-animation: wheel linear 0.2s infinite; animation: wheel linear 0.2s infinite; }
.more-ring:after{left: 80px;}
 @-webkit-keyframes loaing-animation {
  0% { background-position: 10% 0;}
  100% {background-position: 100% 0; }
}
@keyframes loaing-animation {
  0% { background-position: 10% 0;}
  100% {background-position: 100% 0; }
}

@-webkit-keyframes wheel{
  0% {-webkit-transform:rotate(0deg);}
  100% {-webkit-transform:rotate(360deg);}
}
@keyframes wheel{
  0% {transform:rotate(0deg);}
  100% {transform:rotate(360deg);}
}
</style>



